import { Card, CardContent } from "@/components/ui/card";
import { fetchVehicles } from "@/lib/data";
import { getImageById } from "@/lib/placeholder-images";
import Image from "next/image";

export default async function GalleryPage() {
    const vehicles = await fetchVehicles();
    const allImages = vehicles.flatMap(v => v.images.map(id => getImageById(id)).filter(Boolean));

    // Simple deduplication based on imageUrl
    const uniqueImages = Array.from(new Map(allImages.map(item => [item?.imageUrl, item])).values());


    return (
        <div className="container mx-auto px-4 py-12">
            <div className="mx-auto max-w-3xl text-center">
                <h1 className="font-headline text-4xl font-bold md:text-5xl">LMC Gallery</h1>
                <p className="mt-4 text-lg text-muted-foreground">
                    A collection of our finest vehicles.
                </p>
            </div>
            <div className="mt-12">
                <div className="grid grid-cols-1 gap-4 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4">
                    {uniqueImages.map((image) => image && (
                        <Card key={image.id} className="overflow-hidden">
                            <CardContent className="p-0">
                                <div className="relative aspect-square">
                                    <Image 
                                        src={image.imageUrl} 
                                        alt={image.description}
                                        fill
                                        className="object-cover transition-transform duration-300 hover:scale-105"
                                        data-ai-hint={image.imageHint}
                                        sizes="(max-width: 640px) 100vw, (max-width: 768px) 50vw, (max-width: 1024px) 33vw, 25vw"
                                    />
                                </div>
                            </CardContent>
                        </Card>
                    ))}
                </div>
            </div>
        </div>
    );
}
